.button {
    font: inherit;
    padding: 0.5rem 1.5rem;
    border: 1px solid  #2f0b72;
    border-radius: 4px;
    background:  #2f0b72;
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-right: 1rem;
    text-decoration: none;
    display: inline-block;
  }
  
  .button:focus {
    outline: none;
  }
  
  .button:hover,
  .button:active {
    color:black;
    background:#efb9f2;
    border-color: #efb9f2;
  }
  
  .button--inverse {
    background: transparent;
    color: #2f0b72;
  }
  
  .button--inverse:hover,
  .button--inverse:active {
    color: rgb(0, 0, 0);
    background: #efb9f2;
  }
  
  .button--danger {
    background:  #2f0b72;
    border-color:  #2f0b72;
  }
  
  .button--danger:hover,
  .button--danger:active {
    background: #efb9f2;
    border-color: #efb9f2;
  }
  
  .button:disabled,
  .button:hover:disabled,
  .button:active:disabled {
    background: #ccc;
    color: #979797;
    border-color: #ccc;
    cursor: not-allowed;
  }
  
  .button--small {
    font-size: 0.8rem;
  }
  
  .button--big {
    font-size: 1.5rem;
  }